import { defineStore } from "pinia";
import axios from "axios";

const RICK_AND_MORTY_URL = `${process.env.VUE_APP_RICK_AND_MORTY_URL}`;

export const useRickMortyStore = defineStore("rickMorty", {
  state: () => {
    return { charactersListData: {}, episodesListData: [], idsList: [] };
  },
  getters: {
    charactersList: (state) => state.charactersListData,
    episodesList: (state) => state.episodesListData,
    listOfIds: (state) => state.idsList,
  },
  actions: {
    getId(str) {
      let splittedStr = str.split("/");
      let id = splittedStr[splittedStr.length - 1];
      return id;
    },

    async getCharactersList(
      currentPage,
      status = "" || null,
      name = " " || null
    ) {
      console.log(status, name);
      let result = {};
      let page = 1;
      if (currentPage) {
        page = currentPage;
      }
      if (status) {
        if (name) {
          result = await axios.get(
            `${RICK_AND_MORTY_URL}/character/?page=${page}&name=${name}&status=${status}`
          );
        } else {
          result = await axios.get(
            `${RICK_AND_MORTY_URL}/character/?page=${page}&status=${status}`
          );
        }
      } else {
        if (name) {
          result = await axios.get(
            `${RICK_AND_MORTY_URL}/character/?page=${page}&name=${name}`
          );
        } else {
          result = await axios.get(
            `${RICK_AND_MORTY_URL}/character/?page=${page}`
          );
        }
      }

      if (result.status == 200) {
        let resultData = result.data.results;
        resultData.forEach((elem) => {
          let splittedElem = elem.episode[0].split("/");
          let id = splittedElem[splittedElem.length - 1];
          if (this.idsList.filter((elem) => elem === id).length == 0) {
            this.idsList.push(id);
          }
        });
      }

      this.$state.charactersListData = result.data;
      this.getCurrentEpisode(this.idsList);
      return result;
    },

    async getCurrentEpisode(idsList) {
      let notInList = [];
      idsList.forEach((id) => {
        if (
          this.episodesListData.filter((elem) => this.getId(elem.url) == id)
            .length == 0
        ) {
          notInList.push(id);
        }
      });

      if (notInList.length > 0) {
        const result = await axios.get(
          `${RICK_AND_MORTY_URL}/episode/${notInList.join(",")}`
        );
        if (result.data.length > 1) {
          result.data.forEach((elem) => {
            this.episodesList.push({ name: elem.name, url: elem.url });
          });
        } else if (result.data.length == 1) {
          this.episodesList.push({
            name: result.data[0].name,
            url: result.data[0].url,
          });
        }
        return result;
      }
    },
    async getAllEpisodesLinks() {},
    async getCharacter(id) {
      const result = await axios.get(`${RICK_AND_MORTY_URL}/character/${id}`);

      return result;
    },
  },
});
